.landing-page {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 50px;
}

.landing-page__title {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: #ecdc25 10px solid;
  width: 300px;
  background-color: #eb2944;
  border-radius: 25px;
  font-size: 75px;
  font-weight: 900;
}

.landing-page__diaries {
  display: flex;
  flex-direction: column;
  justify-content: center;
  border: #ecdc25 10px solid;
  flex-grow: 0;
  width: 300px;
  background-color: #499bff;
  border-radius: 25px;
}

